import * as THREE from "three";
import React, { useEffect, useState, useRef } from "react";
import { createRoot } from "react-dom/client";
import { useFrame, useThree } from "@react-three/fiber";
import { useGLTF, ScrollControls, useScroll, useAnimations } from "@react-three/drei";
import { Box } from "@mui/material";

import { scrollStops } from "./data/ScrollStopData";
import { scenePath } from "@/app/Routing";
import { ModelData } from "@/interfaces/CoreInterfaces";
import InfoWindow from "@/components/2d/InfoWindow";

import { useDebug } from "./App";

const ScrollDemo: React.FC<{ modelData: ModelData; scrollLength?: number }> = ({ modelData, scrollLength = 1 }) => {
	return (
		// Wrap the scene in ScrollControls. Adjust pages as needed.
		<ScrollControls pages={scrollLength}>
			<ScrollModel modelData={modelData} stopDelay={600} />
		</ScrollControls>
	);
};

export default ScrollDemo;

function ScrollModel({ modelData, stopDelay = 1000 }: { modelData: ModelData; stopDelay?: number }) {
	const { camera } = useThree();
	const scroll = useScroll();
	const modelPath = scenePath + modelData.path;
	const { scene, animations } = useGLTF(modelPath);
	const RS_Camera = scene.getObjectByName("RS_Camera");
	const Rotor = scene.getObjectByName("Rotor");
	const { actions } = useAnimations(animations, scene);
	const [targetOffset, setTargetOffset] = useState(0);
	useEffect(() => void (actions["Animation"].play().paused = true), [actions]);

	// Variables for the scroll stops
	const stops = scrollStops;
	const stopThreshold = 0.08; // When within 2% of a stop, lock to it
	const [activeStop, setActiveStop] = useState(0);
	const [scrollLocked, setScrollLock] = useState(false);
	const [isAtStop, setIsAtStop] = useState(true);
	const [preventLock, setPreventLock] = useState(false);
	const [scrollOverlayTop, setScrollOverlayTop] = useState("0vh");

	useEffect(() => {
		if (scroll.el) {
			scroll.el.style.overflow = "scroll";
			scroll.el.style.scrollbarWidth = "none"; // Firefox
			/// @ts-expect-error - MS specific property
			scroll.el.style.msOverflowStyle = "none"; // IE & Edge
		}
	}, [scroll]);

	const texturePaths = {
		RobotArm: `${scenePath}/textures/M_RobotArm_occlusionRoughnessMetallic.png`,
		WindTurbine_Base: `${scenePath}/textures/M_Windmill_occlusionRoughnessMetallic.png`,
		WindTurbine_Base2: `${scenePath}/textures/M_WindmillBase_occlusionRoughnessMetallic.png`,
		Tanker: `${scenePath}/textures/M_Tanker_occlusionRoughnessMetallic.png`,
	};

	const usePreloadedTextures = () => {
		const [textures, setTextures] = useState<Record<string, THREE.Texture>>({});
		const loadedRef = useRef(false); // Prevents multiple loads

		useEffect(() => {
			if (loadedRef.current) return; // Ensure textures only load once

			const loader = new THREE.TextureLoader();
			const loadTexture = (path: string) =>
				new Promise<THREE.Texture>((resolve) => {
					loader.load(path, (texture) => {
						texture.wrapS = THREE.RepeatWrapping;
						texture.wrapT = THREE.RepeatWrapping;
						texture.flipY = false;
						resolve(texture);
					});
				});

			// Load all textures asynchronously
			Promise.all(
				Object.entries(texturePaths).map(async ([key, path]) => {
					const texture = await loadTexture(path);
					return { key, texture };
				})
			).then((loadedTextures) => {
				const textureMap = Object.fromEntries(loadedTextures.map(({ key, texture }) => [key, texture]));
				setTextures(textureMap);
				loadedRef.current = true;
			});
		}, []);

		return textures;
	};

	const ApplyTextures = ({ scene }: { scene: THREE.Group }) => {
		const textures = usePreloadedTextures();

		useEffect(() => {
			if (!scene || Object.keys(textures).length === 0) return;

			// Apply textures
			const RobotArm = scene.getObjectByName("RobotArm");
			if (RobotArm) {
				RobotArm.traverse((child) => {
					if (child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial) {
						child.material.aoMap = textures.RobotArm;
						child.material.needsUpdate = true;
					}
				});
			}

			const WindTurbine_Base = scene.getObjectByName("WindTurbine_Base");
			if (WindTurbine_Base) {
				WindTurbine_Base.traverse((child) => {
					if (child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial) {
						child.material.aoMap = textures.WindTurbine_Base;
						child.material.needsUpdate = true;
					}
				});
			}

			if (WindTurbine_Base instanceof THREE.Mesh && WindTurbine_Base.material instanceof THREE.MeshStandardMaterial) {
				WindTurbine_Base.material.aoMap = textures.WindTurbine_Base2;
				WindTurbine_Base.material.needsUpdate = true;
			}

			const Tanker = scene.getObjectByName("Tanker");
			if (Tanker) {
				Tanker.traverse((child) => {
					if (child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial) {
						child.material.aoMap = textures.Tanker;
						child.material.needsUpdate = true;
					}
				});
			}
		}, [scene, textures]); // Runs when scene & textures are ready

		return null;
	};

	scene.traverse((child) => {
		if (child.name.includes("Placeholder")) {
			child.visible = false;
		}
		if (child.name.includes("PlaneToHide")) {
			child.visible = false;
		}
	});

	const checkScroll = () => {
		// Check if the scroll is within the threshold of a stop
		for (let i = 0; i < stops.length; i++) {
			if (Math.abs(scroll.offset - stops[i].offset) < stopThreshold) {
				if (i === activeStop) {
					return;
				}
				// If it is, set the active stop and lock the scroll
				setScrollLock(true);
				setIsAtStop(true);
				scroll.el.style.touchAction = "none";
				setActiveStop(i);
				console.log("Scroll locked to stop: ", i);
				// Unlock the scroll after a delay
				setTimeout(() => {
					setScrollLock(false);
					scroll.el.style.touchAction = "auto";
					console.log("Scroll unlocked");
				}, stopDelay);
				break;
			}
		}
	};

	const handleScrollEvent = (e: Event) => {
		if (scrollLocked) {
			e.preventDefault();
		} else {
			if (isAtStop && !preventLock) {
				setPreventLock(true);
				setTimeout(() => {
					setIsAtStop(false);
					setPreventLock(false);
				}, 1000);
			}
		}
	};

	const updateCamera = () => {
		if (RS_Camera) {
			const worldPos = RS_Camera.getWorldPosition(new THREE.Vector3());
			const worldQuat = RS_Camera.getWorldQuaternion(new THREE.Quaternion());
			camera.position.set(worldPos.x, worldPos.y, worldPos.z);
			camera.quaternion.set(worldQuat.x, worldQuat.y, worldQuat.z, worldQuat.w);
			if (targetOffset < 0.2) {
				camera.near = 0.000002;
				camera.far = 0.1;
			} else if (targetOffset < 0.6) {
				camera.near = 0.00001;
				camera.far = 0.5;
			} else {
				camera.near = 0.01;
				camera.far = 8;
			}
			camera.updateProjectionMatrix();
		} else {
			console.warn("RS_Camera not found in scene.");
		}
	};

	useEffect(() => {
		const uiOverlay = document.getElementById("ui-overlay"); // Ensure this div exists in App.tsx

		if (!uiOverlay) {
			console.error("UI overlay not found!");
			return;
		}

		const scrollOverlayHeight = 200 * stops.length + "vh";

		// Create a container for the scroll overlay
		let scrollOverlay = document.getElementById("scrollOverlay");
		if (!scrollOverlay) {
			scrollOverlay = document.createElement("div");
			scrollOverlay.id = "scrollOverlay";
			scrollOverlay.style.position = "absolute";
			scrollOverlay.style.top = scrollOverlayTop;
			scrollOverlay.style.left = "0";
			scrollOverlay.style.width = "100%";
			scrollOverlay.style.height = scrollOverlayHeight;
			uiOverlay.appendChild(scrollOverlay);
		}

		const root = createRoot(scrollOverlay);
		root.render(
			<>
				{stops.map((stop, index) => (
					<React.Fragment key={index}>
						{stop.poiInfo && (
							<Box
								sx={{
									position: "absolute",
									top: `${stop.offset * (stops.length - 1) * 200 + 50}vh`,
									left: "1rem",
									transform: "translateY(-50%)",
									pointerEvents: "auto",
								}}
							>
								<InfoWindow
									title={stop.poiInfo.title}
									description={stop.poiInfo.description}
									image={stop.poiInfo.image}
									globalPath={stop.poiInfo.globalPath}
								/>
							</Box>
						)}
					</React.Fragment>
				))}
			</>
		);

		return () => {
			root.unmount();
			scrollOverlay?.remove();
		};
	}, [stops, scrollOverlayTop]);

	useEffect(() => {
		scroll.el.addEventListener("wheel", handleScrollEvent, { passive: false });
		scroll.el.addEventListener("touchmove", handleScrollEvent, { passive: false });

		return () => {
			scroll.el.removeEventListener("wheel", handleScrollEvent);
			scroll.el.removeEventListener("touchmove", handleScrollEvent);
		};
	});

	useFrame((state, delta) => {
		// Set the animation to the correct time based on page scroll
		const action = actions["Animation"];
		if (!scrollLocked && !isAtStop) {
			if (!useDebug) {
				checkScroll();
			}
		}

		if (Rotor) {
			Rotor.rotateZ(delta * -1.6);
		}

		if (scrollLocked) {
			scroll.el.scrollTop = stops[activeStop].offset * (scroll.el.scrollHeight - scroll.el.clientHeight);
			setTargetOffset(stops[activeStop].offset);
		} else {
			setTargetOffset(scroll.offset);
		}

		action.time = THREE.MathUtils.damp(action.time, action.getClip().duration * targetOffset, 3, delta);
		setScrollOverlayTop(-(scroll.offset * 200 * (stops.length - 1)) + "vh");
		updateCamera();
		if (useDebug) {
			console.log("Scroll offset: ", scroll.offset);
		}
	});

	return (
		<>
			<primitive object={scene} dispose={null} />
			<ApplyTextures scene={scene} />
			{activeStop !== null && stops[activeStop]?.component && stops[activeStop]?.component()}
		</>
	);
}

import { Text } from "@react-three/drei";
import { basePath } from "@/app/Routing";

// 3D Text Component
interface ThreeDTextProps {
	text: string;
	position?: [number, number, number];
	rotation?: [number, number, number];
	scale?: [number, number, number];
}

export const ThreeDText: React.FC<ThreeDTextProps> = ({ text, position = [0, 0, 0], rotation = [0, 0, 0], scale = [1, 1, 1] }) => {
	return (
		<Text
			font="fonts/RedHatDisplay-SemiBold.ttf"
			color="#ffffff"
			anchorY="top"
			anchorX="left"
			fontSize={0.5}
			position={position}
			rotation={rotation}
			scale={scale}
			maxWidth={3} // Set the maximum width for the text
			lineHeight={1.2} // Adjust the line height if needed
			fillOpacity={1}
		>
			{text}
		</Text>
	);
};
