import React, { useState, Suspense, useRef, useEffect } from "react";
import * as THREE from "three";
import { Canvas } from "@react-three/fiber";
import { Environment, Stats, useHelper } from "@react-three/drei";
import { EffectComposer, ToneMapping, N8AO } from "@react-three/postprocessing";
import { SoftShadows } from "@react-three/drei";

// Contexts
import { PoiProvider } from "@/context/PoiContext";

// Custom 3D components
import LoadModels from "@/utils/LoadModels";
import ScrollDemo from "./ScrollDemo";
import DynamicCameraFov from "@/utils/camera/DynamicCameraFov";

// Custom 2D components
import HamburgerMenu from "@/components/2d/HamburgerMenu";
import LoadingSpinner from "@/components/2d/LoadingSpinner";

// Data & interfaces
import { projectData } from "./data/projectData";
import CreateCameraTarget from "@/utils/camera/AddCameraTarget";

export let useDebug: boolean | null = false;

const App: React.FC = () => {
	const canvasRef = useRef<HTMLCanvasElement>(null);
	const overlayRef = useRef<HTMLDivElement>(null);
	const uiRef = useRef<HTMLDivElement>(null);
	const [showStats, setShowStats] = useState(false);
	const handleToggleStats = () => {
		setShowStats((prev) => !prev);
	};
	const [performanceMode, setPerformanceMode] = useState(false);
	const handlePerformanceMode = () => {
		setPerformanceMode((prev) => !prev);
	};
	const [debugMode, setDebugMode] = useState(false);
	const handleDebugMode = () => {
		setDebugMode((prev) => !prev);
	};

	useEffect(() => {
		useDebug = debugMode;
	}, [debugMode]);

	return (
		<div style={{ width: "100vw", height: "100vh", overflow: "hidden", backgroundColor: "#333333" }}>
			{/*<div // Div to create a 1:1 aspect ratio for the canvas
        style={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          width: "100vmin",
          height: "100vmin",
        }}
      > */}
			<PoiProvider initialPoi={projectData.parentPoi}>
				<Canvas
					ref={canvasRef}
					camera={{
						position: new THREE.Vector3(0, 2, 10),
						near: 0.000002,
						far: 0.1,
					}}
					gl={{
						powerPreference: "high-performance",
						alpha: false,
						antialias: true,
					}}
					shadows={performanceMode ? false : true}
				>
					<color attach="background" args={["#333333"]} />
					<Suspense fallback={<LoadingSpinner />}>
						<LoadModels modelData={projectData.sceneModels} />
						<Environment files={"/3d-web-demos/images/hdri/blurred.hdr"} />
						<Light debugMode={debugMode} />
						<DynamicCameraFov frameSize={6} />
						<CreateCameraTarget />
						<ScrollDemo modelData={projectData.sceneModels[0]} scrollLength={10} />
						<fog attach="fog" args={["#333333", 4, 6]} />
						{showStats && <Stats />}
						{!performanceMode && <Effects />}
					</Suspense>
				</Canvas>
				{/* UI Overlay */}
				<div id="overlay" ref={overlayRef} className="absolute left-0 top-0 h-full w-full pointer-events-none overflow-hidden" />

				<div
					ref={uiRef}
					id="ui-overlay"
					style={{ position: "absolute", top: 0, left: 0, width: "100%", height: "100%", pointerEvents: "none", overflow: "hidden" }}
				>
					<HamburgerMenu
						showStats={showStats}
						onToggleStats={handleToggleStats}
						usePerformanceMode={performanceMode}
						onTogglePerformanceMode={handlePerformanceMode}
						debugMode={debugMode}
						onToggleDebugMode={handleDebugMode}
					/>
				</div>
			</PoiProvider>
			{/*</div>*/}
		</div>
	);
};

export default App;

interface LightProps {
	debugMode: boolean;
}

const Light: React.FC<LightProps> = ({ debugMode }) => {
	const shadowCamRef = useRef<THREE.OrthographicCamera>();

	useHelper(debugMode ? shadowCamRef : null, THREE.CameraHelper); // This creates a CameraHelper for the object referenced

	return (
		<group>
			<directionalLight color={"#ffe0cc"} position={[5, 5, -10]} castShadow intensity={20} shadow-mapSize={[1024, 1024]} shadow-bias={-0.01}>
				<orthographicCamera ref={shadowCamRef} attach="shadow-camera" args={[-5, 5, 5, -2, 10, 16]} />
			</directionalLight>
			<directionalLight position={[-5, 5, 5]} intensity={0.4} shadow-mapSize={[128, 128]} />
			<directionalLight position={[-5, 5, -5]} intensity={0.4} shadow-mapSize={[128, 128]} />
			<directionalLight position={[0, 5, 0]} intensity={0.4} shadow-mapSize={[128, 128]} />
		</group>
	);
};

const Effects = () => {
	return (
		<>
			<EffectComposer>
				<ToneMapping />
				{/*<N8AO aoRadius={1} distanceFalloff={1} intensity={0.6} />*/}
			</EffectComposer>
			{/*<SoftShadows size={20} focus={1} samples={32} />*/}
		</>
	);
};
